import React, {Fragment} from 'react';
import classes from "./ImageView.module.css";
import {Helmet} from "react-helmet";
import { HashLink as Link } from 'react-router-hash-link';

import Layout from '../../../../../Components/Layout/Layout';
import SwiftCodeBlock from '../../../../../Components/SwiftCodeBlock/SwiftCodeBlock';
import SeeAlso from '../../../../../Components/SeeAlso/SeeAlso';

function App() {
  return (
    <Fragment>
        <Helmet>
          <title>Text View - ImageView Swift | TheCodeClass</title>
        </Helmet>

    <Layout mainTitle = "iOS References"
            longText = "How to create an Image View in iOS"
            keyText = "Image View - iOS Swift"
            youtubesrc = ""
            downloadHref = "https://github.com/evannwes/reference-ios-uikit-imageview/archive/master.zip"
            referenceHref = "https://developer.apple.com/documentation/uikit/uiimageview"
            styling = {classes.textviewContent}> 
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Image%20View/Image%20View.png?raw=true"
         alt = "UIImageView iOS Apple" 
         style = {{width: "200px"}}/><br/>
      <small>Photo by Tyler Lastovich on Unsplash</small>

      <p>Not all data is text. Image View allows you to display graphics for decorative purposes or 
      provide information for the user. You can display image such as .jpg or .png images. However,
      image view also allow interaction from the user.</p>

      <h2>How to add Image to Xcode?</h2>
      <p>To add an image to Xcode, go to Assets.xcassets and drag and drop a .jpg or .png image from the Finder window 
      to the image into Assets.xcassets folder.</p>
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Image%20View/Dragging%20the%20Image.png?raw=true"
         alt = "Adding images to xcode" 
         style = {{width: "400px"}}/><br/>

      <h3>1x, 2x, 3x meaning (Scale Factor)</h3>
      <p>iOS uses point as an measurement to place content on screen which map it to pixels in display.
      <strong>1x</strong> means that one pixal is equal to one point. For example, 1x image that has a 
      resolution of 100px  100px will be 200px  200px in 2x and 300px  300px in 3x. Depending on the device
      the have different scale factor.</p>

      <h2>How to create an Image View?</h2>
      <p id = "attributes"><b className = "step">Step 1: </b> In Main.storyboard file, click on the + icon to open the Object 
            Library window and drag and drop a image view on the view.</p>
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Image%20View/Image%20view%20in%20library.png?raw=true"
          alt = "Images view in library"
          style = {{width: "500px"}}
          id = "attributes"
      />
      <p id = "attributes"><b className = "step">Step 2: </b> Choose the image you want to use for the 
      image view by changing the image name in the Attributes panel. (Click on the Attributes Inspector 
      icon or go to Choose View  Inspectors  Show Attributes Inspector.)</p>
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Image%20View/Image%20View%20panel.png?raw=true"
          alt = "Images selection for UIImageView"
          style = {{width: "300px"}}
          id = "attributes"
      />
      <h4>Content Mode (How Images Are Scaled)</h4>
      <p>ContentMode property specify how the image is display in the UIImageview. If the size of the image
      changes, it will scale accordingly to the content mode you stated.</p>
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Image%20View/%20contentMode%20.png?raw=true"
          alt = "Images selection for UIImageView"
          style = {{width: "300px"}}
          id = "attributes"
      />
      <table>
        <thead>
          <tr>
            <th className = "column1">Attributes</th>
            <th className = "thSpacing">Meaning</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th className = "column1">Aspect Fit </th>
            <th className = "column2">Stretch the image to fill up the width and height of the image view.</th>
          </tr>
          <tr>
            <th className = "column1">Scale to Fill </th>
            <th className = "column2">Scales the image until the biggest side reach the maximum height or width.</th>
          </tr>
          <tr>
            <th className = "column1">Aspect Fill</th>
            <th className = "column2">Scales the image until the smallest side reach the maximum height or width.</th>
          </tr>
          <tr>
            <th className = "column1">Redraw</th>
            <th className = "column2">Stretch the image to fill up the width and height of the image view.
            Similair to aspect fit. However, there is a difference on how they are draw.</th>
          </tr>
          <tr>
            <th className = "column1">Center | Top | Bottom | Left | Right</th>
            <th className = "column2">Displays the image center, Top, Bottom, Left or Righ within the 
            image view. If the image is larger than the image view, 
            then the rest of the image appears outside of the image view.</th>
          </tr>
          <tr>
            <th className = "column1">Top Left | Top Right</th>
            <th className = "column2">Displays the top left or top right of the image within the image view. If the 
            image is larger than the image view, then the rest of the image appears outside of the 
            image view.</th>
          </tr>
          <tr>
            <th className = "column1">Bottom Left | Bottom Right</th>
            <th className = "column2">Displays the bottom left or bottom right of the image within the image view. If the 
            image is larger than the image view, then the rest of the image appears outside of the 
            image view.</th>
          </tr>
        </tbody>
      </table>
      <p><b className = "step">Step 3:</b> Changing the attribute with code. 
      Connect your progess view to ViewController.swift file. 
      (Click on the progess view, hold down the control key, and drag from the progess view to the 
      ViewController.swift file relase and a popup window appears.)</p>

      <p>Choose the following parameters: connection equals <b>IBOutlet</b>, type equals <b> UIImageView</b> and give a name to it. </p>
      <SwiftCodeBlock>
        <span className = "purple">@IBOutlet weak var </span><span className ="blue">imageviewName:</span> <span className = "purpleLight">UIImageView!</span>
      </SwiftCodeBlock>

      <p>Chnaging the attribute</p>
      <SwiftCodeBlock>
        <span className = "blue">imageviewName</span>.<span className ="purpleLight">image</span> = #imageLiteral(resourceName: "imagename") <br/>
        <span className = "blue">imageviewName</span>.<span className ="purpleLight">contentMode</span> = .<span className ="purpleLight">scaleAspectFit</span> 
      </SwiftCodeBlock>

      <p><b className = "step">Step 4:</b> Interactive image view.</p>
      <p>By default, an image view all touch interaction and therefore, you have to enable the it by
      either checking the User Interaction Enabled check box or using code.</p>
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Image%20View/imageview%20interaction.png?raw=true"
          alt = "Images selection for UIImageView"
          style = {{width: "300px"}}
          id = "attributes"
      /> <br/> <br/>
      <SwiftCodeBlock>
        <span className = "blue">imageviewName</span>.<span className ="purpleLight">isUserInteractionEnabled</span> = <span className = "purple">true</span>
      </SwiftCodeBlock>

      <p><strong>Responding to Touch Events : </strong>There are two ways to handle the touch events. 
      One is by attaching gesture recognizers or using ther event handling techniques</p>
      <p><strong>Event Handling:</strong> <br/>
         Type the following method and write a if statement to detect where the user touches.</p>
      <SwiftCodeBlock>
      <span className = "purple">override func </span><span className ="blue">touchesBegan(</span> <span className = "purpleLight">_</span> touches: <span className = "purpleLight">Set &lt;UITouch&gt; </span>, with event: <span className = "purpleLight">UIEvent?)</span>&#123;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">let</span> touch = touches.<span className = "purpleLight">first</span><br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">if</span> touch?.<span className = "purpleLight">view</span> == <span className = "blue">imageviewName</span> &#123;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purpleLight">print</span>(<span className = "red">"Touched Image"</span>)<br/>
      &nbsp;&nbsp;&nbsp;&nbsp; &#125;
      <br/> &#125;
      </SwiftCodeBlock>

      <p><strong>Gesture recognizers: </strong><br/>
         1) Click on the + icon to open the Object Library window and drag and drop a 
         an gesture recognizers such as Tap Gesture Recognizer <strong>on the image view.</strong></p>
         <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Image%20View/tap.png?raw=true"
          alt = "Tap gesture in library"
          style = {{width: "500px"}}
          id = "attributes"
      />

      <p>2) Click on the gesture recognizers, hold down the Control key, and drag from the gesture recognizers
       to the ViewController.swift file release and a popup window appears.</p>
      <p>Choose the following parameters: connection equals Action, type equals <strong>UITapGestureRecognizer </strong>
      and give a name to it.</p>
      <SwiftCodeBlock>
      <span className = "purple">@IBAction func </span><span className ="blue">tappedOnImage(</span> <span className = "purpleLight">_</span> sender: <span className = "purple">UITapGestureRecognizer</span>)&#123;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purpleLight">print</span>(<span className = "red">"Touched Image"</span>)
      <br/> &#125;
      </SwiftCodeBlock>



      


      <SeeAlso>
        <Link><span className = "alink">Steppers' appearance</span></Link>
        <p className = "subtext">Changing the appearance of stepper using images and colors.</p>
        <Link><span className = "alink">UISliders</span></Link>
        <p className = "subtext">Similar to stepper. UISliders allow you to change the number without
        typing the numerous number.</p>
      </SeeAlso>
    </Layout>
    </Fragment>
  );
}

export default React.memo(App);
