import React, {Fragment} from 'react';
import classes from "./Textfield.module.css";
import {Helmet} from "react-helmet";
import { HashLink as Link } from 'react-router-hash-link';

import Layout from '../../../../../Components/Layout/Layout';
import SwiftCodeBlock from '../../../../../Components/SwiftCodeBlock/SwiftCodeBlock';
import SeeAlso from '../../../../../Components/SeeAlso/SeeAlso';

function App() {
  return (
    <Fragment>
        <Helmet>
          <title>Text View - iOS Swift | TheCodeClass</title>
        </Helmet>

    <Layout mainTitle = "iOS References"
            longText = "How to create Progress Text in iOS"
            keyText = "Text View - iOS Swift"
            youtubesrc = ""
            downloadHref = "https://github.com/evannwes/reference-ios-uikit-textview/archive/master.zip"
            referenceHref = "https://developer.apple.com/documentation/uikit/uitextview"
            styling = {classes.textviewContent}> 
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Text%20View/Text%20View.png?raw=true"
         alt = "UITextView iOS Apple" 
         style = {{width: "200px"}}/>

      <p>Text Fields only able to display limting amount of text. In order to overcome this, you can you
      a Text Views instead. Text Views is a larger version of text fields allowing you to display 
      larger amount of text or/and allow user to edit and input the text.</p>

      <h2>How to create a Progress View?</h2>
      <p id = "attributes"><b className = "step">Step 1: </b> In Main.storyboard file, click on the + icon to open the Object 
              Library window and drag and drop a text view on the view.</p>
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Text%20View/Text%20View%20Library.png?raw=true"
          alt = "Progress view in library"
          style = {{width: "500px"}}
          id = "attributes"
      />
      <p><b className = "step">Step 2:</b> Click on the Attributes Inspector icon or go to Choose View  Inspectors  Show Attributes Inspector.
            You will be able to see <b>attributes available for a text view.</b> Change accordingly to the properties you want.</p>

      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Text%20View/Text%20View%20Panel.png?raw=true"
        alt = ""
        style = {{width: "300px"}}/>
      <table>
        <thead>
          <tr>
            <th className = "column1">Attributes</th>
            <th className = "thSpacing">Meaning</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th className = "column1">Editable</th>
            <th className = "column2">Determine if a text view can be edited</th>
          </tr>
          <tr>
            <th className = "column1">Selectable</th>
            <th className = "column2">Determine if text view can be select or not.</th>
          </tr>
          <tr>
            <th className = "column1">Data Detector</th>
            <th className = "column2">Convert the text into a clickable URL or link. For example, if 
            phoneNumber is ticked. The phone number appears on the text view will be highlighted and if a user
            click on it then an iPhone can call this number.</th>
          </tr>
        </tbody>
      </table>

      <table>
        <thead>
          <tr>
            <th className = "column1">Data Detector Types</th>
            <th className = "thSpacing">Meaning</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th className = "column1">Phone Number</th>
            <th className = "column2">Detects phone numbers.</th>
          </tr>
          <tr>
            <th className = "column1">Link</th>
            <th className = "column2">Detects URLs.</th>
          </tr>
          <tr>
            <th className = "column1">Address</th>
            <th className = "column2">Detects street addresses.</th>
          </tr>
          <tr>
            <th className = "column1">Calendar Event</th>
            <th className = "column2">Detects times and dates.</th>
          </tr>
          <tr>
            <th className = "column1">Shipment Tracking Number</th>
            <th className = "column2">Detects package tracking numbers.</th>
          </tr>
          <tr>
            <th className = "column1">Flight Number</th>
            <th className = "column2">Detects airline flight numbers.</th>
          </tr>
          <tr>
            <th className = "column1">Lookup Suggestion</th>
            <th className = "column2">Detects words or phrases such as restaurant names and etc.</th>
          </tr>
        </tbody>
      </table>

      <p>Attributes that only able to modify via code.</p>
      <table>
        <thead>
          <tr>
            <th className = "column1">Attributes</th>
            <th className = "thSpacing">Meaning</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th className = "column1">allowsEditingTextAttributes</th>
            <th className = "column2">Enable user to edit the text properties if it is true. Default: false<br/>   
                                                  <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Text%20View/allowsEditingTextAttributes.png?raw=true"
                                                       alt = "Allowed editing text attributes. Text view attributes."
                                                       style = {{width:"50px"}}
                                                  /></th>
          </tr>
          <tr>
            <th className = "column1">clearsOnInsertion</th>
            <th className = "column2" id = "attributescode">Allow you to insert text while removing the old content if it is true. Default: false<br/>
                                                  <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Text%20View/clearsOnInsertion.png?raw=true"
                                                       alt = "Allowed editing text attributes. Text view attributes."
                                                       style = {{width:"200px"}}
                                                  />
                                                  </th>
          </tr>
        </tbody>
      </table>

      <p><b className = "step">Step 3:</b>Connect your Text Field to ViewController.swift file. 
      (Click on the Text Field, hold down the control key, and drag from the progess view to the 
      ViewController.swift file relase and a popup window appears.)</p>
      <p>Choose the following parameters: connection equals <b>IBOutlet</b>, type equals <b> UITextView</b> and give a name to it. </p>
      <SwiftCodeBlock>
        <span className = "purple">@IBOutlet weak var </span><span className ="blue">textviewName:</span> <span className = "purpleLight">UITextView!</span>
      </SwiftCodeBlock>

      <p><strong>You can update the progess </strong> by changing the progess view's progess </p>
      <SwiftCodeBlock>
        <span className = "blue">textviewName</span>.<span className ="purpleLight">isEditable</span> = <span className = "purple">true</span> <br/>
        <span className = "blue">textviewName</span>.<span className ="purpleLight">isSelectable</span> = <span className = "purple">true</span> <br/>
        <span className = "blue">textviewName</span>.<span className ="purpleLight">dataDetectorTypes</span> = <span className = "purpleLight">UIDataDetectorTypes</span>.<span className = "purpleLight">link</span> <br/>
        <span className = "blue">textviewName</span>.<span className ="purpleLight">dataDetectorTypes</span> = <span className = "purpleLight">UIDataDetectorTypes</span>.<span className = "purpleLight">address</span> <br/> <br/>
        <span className = "blue">textviewName</span>.<span className ="purpleLight">allowsEditingTextAttributes</span> = <span className = "purple">true</span> <br/>
        <span className = "blue" id = "userinteraction">textviewName</span>.<span className ="purpleLight">clearsOnInsertion</span> = <span className = "purple">true</span>
      </SwiftCodeBlock>

      <p><b className = "step">Step 4:</b>UITextViewDelegate protocol defines some methods that allow  
      you to detect the user interaction such as start editing, end editing and changes
      made in the textview.</p>
      <table>
        <thead>
          <tr>
            <th className = "column1">Attributes</th>
            <th className = "thSpacing">Meaning</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th className = "column1">textDidBeginEditingNotification</th>
            <th className = "column2">Get notify when a user start editing the textview.</th>
          </tr>
          <tr>
            <th className = "column1">textDidEndEditingNotification</th>
            <th className = "column2">Get notify when a user end its editing in the textview.</th>
          </tr>
          <tr>
            <th className = "column1">textDidChangeNotification</th>
            <th className = "column2">Get notify when there is any changes in the textview.<br/></th>
          </tr>
        </tbody>
      </table>

      <SwiftCodeBlock>
        <span className = "purple">class</span><span className = "blue">ViewController</span>: <span className = "purpleLight">UIViewController, UITextViewDelegate</span> &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">@IBOutlet weak var </span><span className ="blue">textviewName:</span> <span className = "purpleLight">UITextView!</span> <br/> <br/>

        &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">override func </span><span className ="blue">viewDidLoad</span>() &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">super</span>.<span className ="purpleLight">viewDidLoad</span>() <br/> <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className ="blue">textviewName</span>.<span className ="purpleLight">delegate</span> = <span className ="puple">self</span> <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&#125; <br/><br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">func </span><span className ="blue">textViewDidBeginEditing</span>(<span className = "red">_</span> textView: <span className = "purpleLight">UITextView!</span>) &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purpleLight">print</span>(<span className = "red">"Begin"</span>) <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&#125; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">func </span><span className ="blue">textViewDidEndEditing</span>(<span className = "red">_</span> textView: <span className = "purpleLight">UITextView!</span>) &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purpleLight">print</span>(<span className = "red">"End"</span>) <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&#125; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">func </span><span className ="blue">textViewDidChange</span>(<span className = "red">_</span> textView: <span className = "purpleLight">UITextView!</span>) &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purpleLight">print</span>(<span className = "red">"Changed"</span>) <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;&#125; <br/>
        &#125; <br/>
      </SwiftCodeBlock>



      <SeeAlso>
        <Link><span className = "alink">Steppers' appearance</span></Link>
        <p className = "subtext">Changing the appearance of stepper using images and colors.</p>
        <Link><span className = "alink">UISliders</span></Link>
        <p className = "subtext">Similar to stepper. UISliders allow you to change the number without
        typing the numerous number.</p>
      </SeeAlso>
    </Layout>
    </Fragment>
  );
}

export default React.memo(App);
