import React, {Fragment} from 'react';
import classes from "./Sliders.module.css";
import {Helmet} from "react-helmet";
import { HashLink as Link } from 'react-router-hash-link';

import Layout from '../../../../../Components/Layout/Layout';
import SwiftCodeBlock from '../../../../../Components/SwiftCodeBlock/SwiftCodeBlock';
import SeeAlso from '../../../../../Components/SeeAlso/SeeAlso';

function App() {
  return (
    <Fragment>
        <Helmet>
          <title>Sliders - iOS Swift | TheCodeClass</title>
        </Helmet>

    <Layout mainTitle = "iOS References"
            longText = "How to slider in iOS?"
            keyText = "Sliders - iOS Swift"
            youtubesrc = ""
            downloadHref = "https://github.com/evannwes/reference-ios-uikit-sliders/archive/master.zip"
            referenceHref = "https://developer.apple.com/documentation/uikit/uislider"
            styling = {classes.slidersContent}> 

            <br/>
            <p>A slider let user to selected a specific number without typing or clicking it. Slider
            makes it easy for user to to chosse between a range of number by dragging the position of
            the slider's position.</p>

            <p>As compared to UIStepper, slider has an disadvantage as it take up more spaces 
            then steppers and also, stepper allowed you to increase or decrease in a fixed value.</p>

            <h2>Anatomy of UISlider</h2>
            <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Sliders/Anatomy.png?raw=true"
                 alt = "Anatomy of UISlider"
                 style = {{width: "350px"}}
            />
            <p>Slider consists of three main parts:</p>
            <ol>
              <li>Thumb - Circle portion.</li>
              <li>Minimum track - Blue color portion</li>
              <li>Maximum track - Grey color portion</li>
            </ol>
            <p> However, you can change colors for the thumb, minimum, and maximum track to make them more visible 
            or a better UX experience.</p>

            <h2>How to create a slider?</h2>
            <p id = "attributes"><b className = "step">Step 1: </b> In Main.storyboard file, click on the + icon to open the Object 
            Library window and drag and drop a slider on the view.</p>
            <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Sliders/Slider%20library.png?raw=true"
                 alt = "Slider in libaray"
                 className = "landscapeImage"/>

            <p><b className = "step">Step 2:</b> Click on the Attributes Inspector icon or go to Choose View  Inspectors  Show Attributes Inspector.
            You will be able to see <b>attributes available for a slider.</b> Change accordingly to the properties you want.</p>
            <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Sliders/Attributes.png?raw=true"
             alt = ""
             style = {{width: "500px"}}/>
            
      <table>
        <thead>
          <tr>
            <th className = "column1">Attributes</th>
            <th className = "thSpacing">Meaning</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th className = "column1">Value</th>
            <th className = "column2">The starting value to be displays.</th>
          </tr>
          <tr>
            <th className = "column1">Minimum value</th>
            <th className = "column2">The lowest value, the slider can represent. </th>
          </tr>
          <tr>
            <th className = "column1">Maximum Value</th>
            <th className = "column2" id = "attributescode">The largest value, the slider can represent.</th>
          </tr>
        </tbody>
      </table>
      <p><b className = "step">Step 3:</b> Connect your slider to ViewController.swift file. (Click 
      on the slider, hold down the Control key, and drag from the slider to the ViewController.swift 
      file relase and a popup window appears.)</p>
      <p>Choose the following parameters: connection equals <b>IBOutlet</b>, type equals <b> UISlider</b> and give a name to it. </p>
      <SwiftCodeBlock>
      <span className = "purple">@IBOutlet weak var </span><span className ="blue">sliderName:</span> <span className = "purpleLight">UISlider!</span>
      </SwiftCodeBlock>

      <p>Changing the properties with code: <a href = "/#/references/ios/uikit/stepper-creation#attributes">click here</a> to see the definition of the properties</p>
      <SwiftCodeBlock>
        <span className = "blue">sliderName</span>.<span className ="purpleLight">value</span> = <span className = "darkblue">6</span> <br/>
        <span className = "blue" id = "IBAction">sliderName</span>.<span className ="purpleLight">maximumValue</span> = <span className = "darkblue">20</span> <br/>
        <span className = "blue">sliderName</span>.<span className ="purpleLight">minimumValue</span> = <span className = "darkblue">2</span> 
      </SwiftCodeBlock>

      <p><b className = "step">Step 4:</b> IBAction: Detecting a user slide on the slider.(Click on the slider, hold down the Control key, 
      and drag from the stepper to the ViewController.swift file release and a popup window appears.)</p>
      <p>Choose the following parameters: connection equals <b>Action</b>, type equals <b> UISlider</b> and give a name to it. <br/>
      The example below change the UILabel to the text accordingly to current slider value.</p>
      <SwiftCodeBlock>
      <span className = "purple">@IBAction func </span><span className ="blue">sliderSlide(</span> <span className = "purpleLight">_</span> sender: <span className = "purple">UISlider</span>)&#123;<br/>
      <span className ="blue tab">textLabel</span>.<span className = "purpleLight">text</span> = <span className = "red">"</span>\( <span className ="purpleLight">sliderName</span>.<span className = "purpleLight">value</span> )<span className = "red">"</span>
      <br/> &#125;
      </SwiftCodeBlock>
      

      <SeeAlso>
        <Link><span className = "alink">Steppers' appearance</span></Link>
        <p className = "subtext">Changing the appearance of stepper using images and colors.</p>
        <Link><span className = "alink">UISliders</span></Link>
        <p className = "subtext">Similar to stepper. UISliders allow you to change the number without
        typing the numerous number.</p>
      </SeeAlso>
    </Layout>
    </Fragment>
  );
}

export default React.memo(App);
